.containerForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 50%;
  height: 500px;
  background-color: var(--UIColor);
}
.welcomeContainer {
  display: flex;
  flex-direction: column;
}
.formDiv {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  background-color: rgba(var(--UIText), 0.3);
  backdrop-filter: blur(2px);
  width: 80%;
  margin-left: 10%;
  border-radius: 15px;
}

.containerLogin {
  display: flex;
  align-items: center;
  width: 80%;
  margin-left: 10%;
}

.headerLogin {
  align-self: center;
  font-size: 35px;
  color: var(--UIText);
}

.textLogin {
  color: var(--UIText);
  font-size: 25px;
}

.buttonLogin {
  color: var(--UIText);
  color: var(--btn_text);
  font-size: fit-content;
  border-radius: 20%;
  width: auto; /* Allow the button to resize based on text length */
  height: 50px;
  margin-top: 5px;
}
.inputLogin {
  font-size: fit-content;
  white-space: nowrap;
  height: 50px;
  width: 80%;
  color: rgb(var(--UIText));
  background-color: rgb(var(--UIColor));
}

.rightDiv {
  background-color: var(--UIText);
  opacity: 1;
  width: 50%;
  height: 500px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  border-top-right-radius: 20%;
  border-bottom-right-radius: 20%;
}
.imgDiv {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
.imgDiv img {
  max-width: 100%;
  max-height: 100%;
  z-index: 10;
}
.registerFooter {
  color: VAR(--UIText);
  display: flex;
  flex-direction: row;
}
.registerNow {
  cursor: pointer;
  text-decoration: underline;
}
.authenticationPage {
  align-items: center;
  padding: 40px;
  font-size: 50px;
  color: #097b5a;
  font-weight: bold;
}
.coolHeadingText {
  font-family: 'ICA Rubrik';
  font-size: 5.7em;
  line-height: 1em;
  color: rgb(var(--UIColor));
  position: absolute;
  top: 60%;
  display: flex;
  font-weight: bold;
}

.coolHeadingText::after {
  position: absolute;
  inset: 0 0 0 0;
  content: attr(data-content);
  z-index: 11;

  -webkit-text-stroke: 3px pink;
  color: transparent;
}
.containerLoginForm {
  display: flex;
  align-items: center;
  width: 80%;
  margin-left: 10%;
}
@media (max-width: 1300px) {
  .containerLogin {
    width: 100%;
    margin-left: 0;
    margin-top: 20%;
  }
  .containerLoginForm {
    width: 100%;
    margin-left: 0;
  }
  .formDiv {
    width: 100%;
    margin: 0;
    border-radius: 0;
    justify-content: space-around;
  }
}
@media (max-width: 1100px) {
  .containerLogin {
    width: 100vw;
    height: calc(100vh - 64px);
    margin-top: 0;
    margin-left: 0;
    flex-direction: column;
  }
}
@media (max-width: 550px) {
  .coolHeadingText {
    font-size: 4em;
  }
  .containerLogin {
    width: 100%;
  }
}
@media (max-width: 400px) {
  .coolHeadingText {
    font-size: 2rem;
    letter-spacing: 5px;
  }
}
